@import url('https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap');

.header-frame {
  position: relative;
  display: flex;
  align-items: stretch;
  justify-content: space-between;
  background-color: #FFE194;
}

.header-title {
  display: flex;
  align-items: center;
  padding: 0 10px 0 10px;
  user-select: none;
  color: green;
  font-family: 'Press Start 2P', cursive;
  font-size: 1em;
}

.header-title img {
  height: 60%;
  padding-top: 2px;
  padding-right: 10px;
  user-select: none;
}

.stretch {
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 1;
}

.header-menu {
  background-color: #FFE194;
  position: absolute;
  top: 30px;
  z-index: 1;
}
