.editor-frame {
  flex-direction: column;
}

.editor-text-frame {
  display: flex;
  flex: 1 1 0%;
}

.editor-text {
  background: white;
  width: 100%;
  font-family: courier;
  font-size: 16px;
  padding: 5px 10px 5px;
  resize: none;
  outline: none !important;
  color: #4c4c4c;
  border:solid 1px #ccc;
  -webkit-border-radius: 4px;
  -webkit-box-shadow: inset 0 0 2px rgba(0, 0, 0, 0.2);
}
