.box-frame {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.25);
}

.box-content {
  min-width: 100px;
  min-height: 50px;
  padding: 10px;
  background-color: white;
  box-shadow: 0 0 10px rgba(0,0,0,0.15);
  border-radius: 10px;
}
